
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import nodata from '@/components/noData'
import ShopBtn from '@/components/shopBtn'
import shopList from '@/components/shopList'
import { getMyOrderList, cancelOrder, deleteOrder, ConfirmOrder, BuyOrderAgain } from '@/api/user'
export default {
name: 'myOrder',
data () {
return {
noDataShow: false,
postData: {
page: 1,
pagenum: 50
},
toBottom: false,
loadingTips: '加载中...',
curTab: 0,
selectItem: '',
tabList: [
{
label: '全部订单',
val: 'all'
},
{
label: '待付款',
val: 1
},
{
label: '待收货',
val: 2
},
{
label: '已完成',
val: 3
},
{
label: '取消/退款',
val: 4
}
],
noData: {
txt: '还没有相关订单呢~',
img: 'static/images/noOrder.png'
},
curData: []
}
},
computed: {

},
filters: {
statusFilter (val) {
let res = ''
switch (val) {
case 'waittingpay':
res = '待付款'
break
case 'payed':
res = '已付款'
break
case 'payedone':
res = '已付定金'
break
case 'waittingsecpay':
res = '待付尾款'
break
case 'sent':
res = '已发货'
break
case 'recieve':
res = '已签收'
break
case 'finish':
res = '已完成'
break
case 'refunding':
res = '退款中'
break
case 'agreerefund':
res = '同意退款'
break
case 'reject':
res = '拒绝退款'
break
case 'waittingconfirm':
res = '退款待确认'
break
case 'refunded':
res = '退款完成'
break
case 'overtime':
res = '超时关闭'
break
case 'cancel':
res = '取消关闭'
break
default:
res = '未知'
break
}
return res
}
},
created () {
console.log(this.curTab)

if (this.$route.query.type) {
console.log(this.$route.query.type)
this.curTab = Number(this.$route.query.type)
this.selectItem = Number(this.$route.query.type)
this.postData.status = this.$route.query.type
this.getOrder()
} else {
this.getOrder()
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
ShopBtn,
shopList,
nodata,
Confirm
},
methods: {
allconfirm (item,stauas) {
var contenttxt = '';
const _that = this
if(stauas==='recieve'){
contenttxt ="确认收货?"
}
else if(stauas==='waittingpay'){
contenttxt ="确认取消此订单?"
}
else if(stauas==='cancel'){
contenttxt ="确认删除此订单?"
}
this.$vux.confirm.show({
title: '温馨提示',
content: contenttxt,

onCancel () {
},
onConfirm () {
_that.$vux.loading.show()
if(stauas==='recieve'){
_that.handleConfirm(item)
}
else if(stauas==='waittingpay'){
_that.handleCancel(item)
}
else if(stauas==='cancel'){
_that.handleDel(item)
}
}
})
},
getOrder () {
this.noDataShow = false
if (this.selectItem !== 'all') {
this.postData.status = this.selectItem
} else {
this.postData.status = ''
}
this.$vux.loading.show()
getMyOrderList(this.postData)
.then(res => {
setTimeout(() => {
this.toBottom = false
}, 2000)
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

if (this.postData.page > 1 && this.postData.page <= Math.ceil(res.data.totalpage / this.postData.pagenum)) {

console.log('concat')
this.curData = this.curData.concat(res.data.data.orderlist)
return
} else if (this.postData.page > Math.ceil(res.data.totalpage / this.postData.pagenum)) {
this.$vux.toast.text('没有更多数据', 'middle')
return
}
this.curData = []
this.curData = res.data.data.orderlist
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
if (this.curData.length < 1) {
this.noDataShow = true
}
})
.catch(err => {
this.$vux.loading.hide()
this.noDataShow = true
setTimeout(() => {
this.toBottom = false
}, 2000)
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
handleSelectItem (item) {
this.postData.page = 1
this.selectItem = item.val
console.log(this.selectItem)
this.getOrder()
},

handleCancel (item) {
console.log(item)
const postData = {
orderid: item.orderid
}
cancelOrder(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('取消成功', 'middle')
this.getOrder()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handlePay (item) {
window.location.href = process.env.BASE_SELF + '/wap/index.html#/nowPay?id=' + item.orderid

/*this.$router.push({
name: 'nowPay',
query: {
id: item.orderid
}
})*/
},

handleConfirm (item) {
const postData = {
orderid: item.orderid
}
ConfirmOrder(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('操作成功', 'middle')
this.postData.status = 3
this.getOrder()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleComment (item) {
this.$router.push({
name: 'uc-submitcomments',
query: {
id: item.orderid
}
})
},

handleLogistics (item) {
this.$router.push({
name: 'uc-orderLogistics',
query: {
id: item.orderid
}
})
},

handleDetail (row) {
console.log(row)
this.$router.push({
name: 'uc-orderDetail',
query: {
id: row.orderid
}
})
},

handleToGoods (row) {
console.log(row)
this.$router.push({
name: 'orderDetail',
query: {
skuid: row.skuid
}
})
},

handleRefundProgress (item) {
this.$router.push({
name: 'uc-refundDetail',
query: {
id: item.orderid
}
})
},

handleDel (item) {
const postData = {
orderid: item.orderid
}
deleteOrder(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('删除成功', 'middle')
this.getOrder()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

buyAgain (item) {
const postData = {
orderid: item.orderid
}
BuyOrderAgain(postData)
.then(res => {
if (res.data.errorcode === 0) {

this.$router.push({path: '/confirmOrder', query: { preid: res.data.data.preid}})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleRefund (item) {
this.$router.push({
name: 'uc-submitRefunds',
query: {
id: item.orderid
}
})
},

loadingMore () {
if (!this.toBottom) {

this.postData.page++
console.log('more')
this.toBottom = true
this.getOrder()
}
},
test () {
console.log(2323)
}
}
}



